    clear: both; }
  .content-wrapper .main-content,
  .content-wrapper .sidebar {
    width: 100%;
    float: right;
    margin-left: 0;
    margin-right: 0;
    clear: none; }
  @media (min-width: 700px) {
    .content-wrapper .main-content {
