/* ============================================================

  Nav Fate Menu

============================================================ */
.nav-menu .pane-title {
  color: #0A497A;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1em;
  padding-left: 0.3125em;
  text-transform: uppercase; }

.nav-menu .pane-menu-tree {
  border-bottom: 1px solid #d3bd00;
  padding: 0.625em 0; }
  .nav-menu .pane-menu-tree .pane-content {
    padding: 0.3125em 0 0; }
  .nav-menu .pane-menu-tree ul.menu {
    margin: 0;
    padding: 0; }
    .nav-menu .pane-menu-tree ul.menu li {
      margin: 0;
      list-style: none;
      list-style-type: none;
      padding: 0; }
    .nav-menu .pane-menu-tree ul.menu a {
      display: block;
      padding-top: 0.3125em;
      padding-bottom: 0.3125em;
      padding-left: 0.3125em; }
      .nav-menu .pane-menu-tree ul.menu a:hover {
        background-color: #ffee53; }
      .nav-menu .pane-menu-tree ul.menu a.active {
        color: #333; }
    .nav-menu .pane-menu-tree ul.menu li {
      border-bottom: 1px solid #d3bd00;
      line-height: 1.1;
      position: relative; }
    .nav-menu .pane-menu-tree ul.menu li.last {
      border-bottom: none;
      margin-bottom: 0; }
    .nav-menu .pane-menu-tree ul.menu ul {
      margin: 0;
      padding: 0;
      font-size: .8em;
      margin-bottom: 0.625em;
      padding-left: 0.625em; }
      .nav-menu .pane-menu-tree ul.menu ul li {
        margin: 0;
        list-style: none;
        list-style-type: none;
        padding: 0; }
      .nav-menu .pane-menu-tree ul.menu ul li {
        line-height: 1.1; }
      .nav-menu .pane-menu-tree ul.menu ul ul {
        font-size: 1em; }

.no-js .nav-menu .pane-menu-tree li:not(.active-trail) ul.menu {
  display: none; }

.js .nav-menu .pane-title {
  cursor: pointer; }
  .js .nav-menu .pane-title:after {
    color: #a09000;
    font-size: .8em; }

.js .nav-menu .menu-closed .pane-title:after {
  font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  display: inline-block;
  margin-left: 5px;
  position: relative;
  top: 1px;
  content: "";
  font-family: 'icomoon';
  text-decoration: none;
  text-rendering: auto; }

.js .nav-menu .menu-closed .pane-content {
  display: none; }

.js .nav-menu .menu-open .pane-title:after {
  font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  display: inline-block;
  margin-left: 5px;
  position: relative;
  top: 1px;
  content: "";
  font-family: 'icomoon';
  text-decoration: none;
  text-rendering: auto; }

.js .nav-menu li.has-menu-control a {
  padding-right: 26px; }

.js .nav-menu .menu-control {
  display: block;
  color: #b9a700;
  cursor: pointer;
  font-size: .9em;
  line-height: 1.7em;
  padding: 0 0.3125em;
  position: absolute;
  top: 0;
  right: 0;
  text-align: center;
  width: 21px; }
  .js .nav-menu .menu-control:hover {
    background-color: #ffee53; }
  .js .nav-menu .menu-control.menu-open:after {
    font-smoothing: antialiased;
    -webkit-font-smoothing: antialiased;
    display: inline-block;
    margin-left: 0;
    position: relative;
    top: 1px;
    content: "";
    font-family: 'icomoon';
    text-decoration: none;
    text-rendering: auto; }
  .js .nav-menu .menu-control.menu-closed:after {
    font-smoothing: antialiased;
    -webkit-font-smoothing: antialiased;
    display: inline-block;
    margin-left: 0;
    position: relative;
    top: 1px;
    content: "";
    font-family: 'icomoon';
    text-decoration: none;
    text-rendering: auto; }
  .js .nav-menu .menu-control.menu-closed + ul.menu {
    display: none; }
  .js .nav-menu .menu-control:after {
    margin: 0; }

/* ============================================================

  Body

============================================================ */
body {
  background: url("../img/tweed.png") #F9F9F9; }

/* ============================================================

  Typography

============================================================ */
body {
  color: #333;
  font-family: "proxima-nova", "Helvetica Neue", Helvetica, Arial, sans-serif;
  line-height: 1.2; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1;
  margin-top: 1.25em;
  margin-bottom: 0.625em; }
  .main-content h1, .main-content h2, .main-content h3, .main-content h4, .main-content h5, .main-content h6 {
    color: #0A497A; }

.main-content {
  background-color: #F9F9F9;
  border-radius: 4px;
  box-shadow: 2px 2px 19px 0 rgba(0, 0, 0, 0.7);
  padding: 2.5em;
  position: relative; }
  .main-content:after, .main-content:before {
    display: block;
    background: inherit;
    border-radius: 4px;
    content: '';
    position: absolute;
    left: 0px;
    top: 0px;
    z-index: -1;
    width: 100%;
    height: 100%;
    transform: rotate(-0.25deg); }
  .main-content:after {
    top: -5px;
    transform: rotate(0.25deg); }
  .main-content .page-title h1 {
    border-bottom: 1px solid #b9b9b9;
    font-size: 2.5em;
    margin-top: 0;
    padding-bottom: 0.3125em; }

